<template>
  <div class="template">
    <div class="top">
      <header>
        <Icon type="document-text" size="23"></Icon>
        <span>模板消息</span>
      </header>
      <p></p>
    </div>
    <div class="content">
      <Tabs v-model="name" @on-click="check">
        <TabPane label="我的模板" name="1">
          <div v-if="name==='1'">
            <my-template @add="add" v-if="flag==='1'" @flags="seeFlag"></my-template>
            <template-info v-if="flag==='0'" @flags="seeFlag"></template-info>
          </div>
        </TabPane>
        <TabPane label="模板库" name="2">
          <templates :infos="data" v-if="name==='2'"></templates>
        </TabPane>
      </Tabs>
    </div>
  </div>
</template>
<script>
  import myTemplate from "./myTemplate.vue"
  import templates from "./templates.vue"
  import templateInfo from  "./templateInfo.vue"
export default{
    data(){
      return{
          name:'1',
          data:[],
          flag:'1',
          templateInfo:''
      }
    },
  methods:{
    seeFlag(val){
     this.flag=val;
    },
    check(val){
        if(val==="2"){
          this.post("custom/loop/template_wx",{page:1}).then(res=>{
            if(res.data.code===10000){
              this.data=res.data.data.list;
            }else{
              this.$Modal.error({
                title: "失败",
                content: res.data.msg
              });
            }
          })
        }
    },
    add(val){
      this.name=val;
      this.post("custom/loop/template_wx",{page:1}).then(res=>{
        if(res.data.code===10000){
          this.data=res.data.data.list;
        }else{
          this.$Modal.error({
            title: "失败",
            content: res.data.msg_test
          });
        }
      })
    }
  },
  components:{myTemplate,templates,templateInfo},
  mounted(){
    this.$store.commit('setStatus',false);
  }
}
</script>
<style scoped>
  .top{  padding: 0.1rem 0;  border-bottom: 1px solid #ebecee;padding-bottom: 0.2rem  }
  header{display: flex;align-items: center;}
  header>span{font-size: 0.18rem;display: inline-block;margin-left: 0.1rem}
  .content{ padding: 0.1rem 0;width: 96%;}
</style>



