<template>
  <div>
    <header style="width: 96%;">
      <p style="font-size: 0.16rem">个人模板库</p>
      <p>
        <span style="color:#9f9f9f">最多可添加25个</span>
        <a href="javascript:void(0)" class="btn" @click="add">添加</a>
      </p>
    </header>
    <div class="list">
      <table>
        <tr>
          <th style="width: 14%">标题</th>
          <th>关键词</th>
          <th>模板ID</th>
          <th style="text-align:left;width: 12%">操作</th>
        </tr>
        <tr class="td" v-for="(item,key) in data">
            <td>{{item.title}}</td>
            <td class="beyond" title="">{{item.keyword_name_list}}</td>
            <td>{{item.template_id}}</td>
            <td>
              <a href="javascript:void(0)" style="margin-left: 0.1rem;" class="info" @click="seeInfo(item,key)">详情</a>
              <a href="javascript:void(0)" class="info"style="margin-left: 0.1rem;"   @click="deleted(item,key)">删除</a>
            </td>
      </tr>
      </table>
      <p v-if="data.length===0"  class="td_data">
        <img src="../../../assets/no-data.png" class="img_box"/>
      </p>
    </div>
  </div>
</template>
<script>
export default{
    data(){
        return {
          msg:"正在加载....",
          data:[],
          appid:''
        }
    },
    methods:{
      seeInfo(item,key){
          this.$emit("flags","0");
          sessionStorage.setItem('templateInfo',JSON.stringify(item));
      },
      deleted(item,key){
        this.$Modal.confirm({
          title: '确认删除',
          content: '<p>确认删除该模板消息</p>',
          onOk: () => {
            this.post("custom/loop/template_del",{template_id:item.template_id}).then(res=>{
              if(res.data.code===10000){
                  this.data.splice(key,1);
                this.$Modal.success({
                  title: "成功删除",
                  content: "成功删除该模板消息"
                });
              }else{
                this.$Modal.error({
                  title: "删除失败",
                  content: res.data.msg_test
                });
              }
            })
          },
        });
      },
      add(){
        this.$emit("add","2")
      }
    },
    mounted(){
        this.appid=this.$route.params.appid;
        this.post("custom/loop/template_user",{}).then( res => {

          if(res.data.code===10000){
            this.data=res.data.data;
            if(this.data.length===0){
                this.msg="暂无数据";
            }
          }else{
            this.msg="暂无数据"
            this.$Modal.error({
              title: "失败",
              content: res.data.msg
            });
          }
        })
  }
}
</script>
<style scoped>
   @import url("../../../../static/table.css");
  header{  display: flex;  justify-content: space-between;  align-items: center;  }
  .btn{  color: white;  display: inline-block;  padding: 0.1rem 0.3rem;  background: #93b719;  border-radius: 0.05rem;  }
  .list{padding: 0.2rem 0; box-shadow: 5px 5px 10px #E7E7E7;}
</style>



