<template>
  <div class="templates">
    <div class="search">
      <div class="item">
        <span style="margin-bottom: 0.1rem;display: inline-block;color: #111111;font-size: 0.14rem;">用户昵称</span>
        <input placeholder="请输入用户昵称" class="input" v-model.trim="name" style="width: 2rem"></Input>
      </div>
      <div class="item item_last">
        <a href="javascript:void(0)" @click="search" class="search_button">搜索</a>
      </div>
    </div>
    <div class="list">
      <table>
        <tr>
          <th style="width: 10%">ID</th>
          <th style="width: 14%">标题</th>
          <th>关键词</th>
          <th style="width: 10%;">操作</th>
        </tr>
        <tr class="td" v-for="(item,key) in data">
          <td>{{item.id}}</td>
          <td>{{item.title}}</td>
          <td class="beyond" title="">{{item.keyword_name_list}}</td>
          <td>
            <a href="javascript:void(0)" class="info" style="display: inline-block;margin-right: 0.08rem" @click="seeInfo(item.id)">选用</a>
          </td>
        </tr>
      </table>
      <p v-if="data.length===0" class="td_data">
        <img src="../../../assets/no-data.png" class="img_box"/>
      </p>
      <footer>
        <page @pageInfo="pages"></page>
      </footer>
    </div>
  </div>
</template>
<script>
  import page from "../../public/page.vue"
  export default {
    data() {
      return {
        msg: "暂无数据",
        data: [],
        appid: ''
      }
    },
    props: ["infos"],
    methods: {
      seeInfo(id) {
        let url = `/${this.appid}/editor/setTemplate/${id}`;
        this.$router.push(url);
      },
      pages(val) {
        this.data = [];
        this.post("custom/loop/template_wx", {
          page: val
        }).then(res => {
          if(res.data.code === 10000) {
            this.data = res.data.data.list;
            if(this.data.length === 0) {
              this.msg = "暂无数据"
            }
          } else {
            this.msg = "暂无数据";
            this.$Modal.error({
              title: "添加失败",
              content: res.data.msg
            });
          }
        })
      },
      search(){
        
      }
    },
    watch: {
      infos(val) {
        this.data = val;
      }
    },
    components: {
      page
    },
    mounted() {
      this.appid = this.$route.params.appid;
    }
  }
</script>
<style scoped>
  @import url("../../../../static/table.css");
  @import '../../../../static/base.css';
  .templates {
    margin: 0.1rem 0.15rem;
  }
  .search{
    width: 100% !important;
  }
</style>


