<template>
    <div style="display: flex;align-items: center;justify-content: flex-end">
      <a href="javascript:void(0)" class="btn" @click="pre" v-if="currentPage!==1"><Icon type="arrow-left-b" size="20" color="#919191"></Icon></a>
      <span style="display: inline-block;margin: 0 0.05rem;">{{currentPage}} /  {{total}}</span>
      <a href="javascript:void(0)" class="btn_btn" @click="next"><Icon type="arrow-right-b" size="20" color="#919191"></Icon></a>
      <input type="number" class="input" v-model="num">
      <a href="javascript:void(0)" class="next" @click="nextTo">跳转</a>
    </div>
</template>
<script>
export default{
    data(){
        return {
          currentPage:1,
          total:20,
          num:''
        }
    },
  methods:{
    pre(){
      this.num="";
      this.currentPage--;
      this.$emit("pageInfo",this.currentPage);
    },
    next(){
        this.num="";
        this.currentPage++;
        this.$emit("pageInfo",this.currentPage);
    },
    nextTo(){
        this.currentPage=(this.num-0);
        this.$emit("pageInfo",this.currentPage);
    }
  }
}
</script>
<style scoped>
.btn_btn{
  display: inline-block;
  padding: 0.05rem 0.12rem;
  border: 1px solid #e4e8eb;
  border-radius: 0.05rem;
}
  .input{
    width: 0.6rem !important;
    padding: 0.07rem 0.1rem;
    border:1px solid  #e4e8eb;
    border-radius: 0.05rem;
    margin: 0 0.1rem;
  }
  .next{
    color: black;
    padding: 0.07rem 0.2rem;
    border:1px solid  #e4e8eb;
    border-radius: 0.05rem;
  }
</style>



